import { Component, OnInit } from '@angular/core';
import { NzMessageService } from 'ng-zorro-antd';
import { UserServiceService } from 'src/app/service/user-service.service';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-campus-equipment-manual',
  templateUrl: './campus-equipment-manual.component.html',
  styleUrls: ['./campus-equipment-manual.component.css']
})
export class CampusEquipmentManualComponent implements OnInit {
  constructor(
    private message:NzMessageService,
    private sanitizer: DomSanitizer,
    private userService: UserServiceService,// doto
  ) { }
  public datastatus ="....."
  public data:any =[];
  loading = true;
  //
  pageSize ="15"
  pageSizeOptions = [ 5, 10, 20];
  //
  isVisible = false;
  //
  showModaType:any;
  //
  qrcodePath:any;
  //pdf
  manualFilePath:any;
  modalTitle:any;
  public equipmentName;
  isShowPdf=false;
  ngOnInit(): void {
    this.initDat();
  }
  public initDat(){
    this.userService.myGet("/equipment/getAll").subscribe(res=>{
      if(res.data.length != 0){
        this.data = res.data
      }else{
        this.datastatus = "";
        this.message.error("")
      }
    })
  }
  public openFram(id,name){
    this.equipmentName =name;   
    this.isVisible = true;
      this.showModaType = 1;
      this.qrcodePath= this.userService.qrpath + name + "_" + id + ".jpg";
      this.modalTitle = name + ""

  }
  public showPdf(id,name,userguidefile){
    if(userguidefile){
      this.isShowPdf = true;
      this.manualFilePath = userguidefile;
      this.manualFilePath = "assets/file/manual_file/6cbbb6dd-9424-421c-b43b-2a0e211036fe_.pdf";
    }else{
      this.message.info("pdf");
    }
  }
  public closePdf(){
    this.isShowPdf = false;
  }

  handleOk(): void {
    this.isVisible = false;
  }

  handleCancel(): void {
    this.isVisible = false;
  }
}
